nav{
    background: var(--black);
    width:max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background:transparent;
    padding: 0.6rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-primary);
    font-size: 0.8rem;
}

nav a:hover{
    background: var(--color-bg);
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}

@media screen and (max-width:1024px){
    nav{
        width: fit-content;
    }
    nav a{
        padding: 0.8rem;
    }
}